import React, { PureComponent, Fragment } from 'react'
import PropTypes from 'prop-types'
import { Responsive, PageSizeSelector } from 'components/table'
import {
    Table,
    TBody,
    Header,
    HeaderCell,
    Row,
    Cell,
    Caption,
} from 'components/table'
import Pagination from 'components/pagination'
import { Loading, Muted } from 'components/text'
import { Br } from 'components/markup'
import snakeCase from 'lodash/snakeCase'
import { NONE, DESC } from 'constants/sortings'
import { StructuredText } from 'prismic/components/base'
import { Documents } from 'prismic/containers'
import * as Predicates from 'prismic/predicates'

// TODO: HOOKS

export default class PrismicTable extends PureComponent {
    static propTypes = {
        value: PropTypes.array.isRequired,
    }
    state = {
        sorting: [null, NONE],
        pageSize: 10,
        page: 1,
    }
    columns = this.props.value.map(createColumn)
    handleSortingChange(name, order) {
        this.setState({
            sorting: [name, order],
            page: 1,
        })
    }
    handlePageSizeChange = pageSize => {
        this.setState({
            pageSize,
            page: 1,
        })
    }
    handlePageChange = page => {
        this.setState({ page })
    }
    getSorting(column) {
        if (column.sortable) {
            const [name, order] = this.state.sorting

            if (column.name === name) {
                return order
            }

            return NONE
        }
    }
    get type() {
        return this.props?.value?.[0]?.source
    }
    get params() {
        const { pageSize, page } = this.state
        const [name, order] = this.state.sorting
        const orderings = []

        if (name && order !== NONE) {
            orderings.push(
                `my.${this.type}.${snakeCase(name)} ${
                    order === DESC ? 'desc' : ''
                }`.trim()
            )
        }

        return {
            predicates: [Predicates.type(this.type)],
            orderings,
            pageSize,
            page,
        }
    }
    renderRow = row => {
        return (
            <Row key={row.id}>
                {this.columns.map(({ name, property }) => (
                    <Cell key={name}>{property(row.data)}</Cell>
                ))}
            </Row>
        )
    }
    renderContent({
        documents = [],
        loading,
        total_pages,
        total_results_size,
    }) {
        this.totalPages = total_pages || this.totalPages

        return (
            <Fragment>
                <Br />
                <Responsive>
                    <Table bordered>
                        <Header>
                            <Row>
                                {this.columns.map(column => (
                                    <HeaderCell
                                        key={column.name}
                                        sorting={this.getSorting(column)}
                                        onSortingChange={this.handleSortingChange.bind(
                                            this,
                                            column.name
                                        )}>
                                        {column.title}
                                    </HeaderCell>
                                ))}
                            </Row>
                        </Header>
                        <TBody>{documents.map(this.renderRow)}</TBody>
                        <Caption>
                            {loading ? (
                                <Loading>Loading documents...</Loading>
                            ) : (
                                <Muted>
                                    {`Total of ${total_results_size} documents
                                    found.`}
                                </Muted>
                            )}
                        </Caption>
                    </Table>
                </Responsive>
                <PageSizeSelector
                    value={this.state.pageSize}
                    onChange={this.handlePageSizeChange}
                    suffix="documents par page"
                />
                <Pagination
                    active={this.state.page}
                    onChange={this.handlePageChange}
                    total={this.totalPages}
                />
            </Fragment>
        )
    }
    render() {
        return (
            <Documents {...this.params}>
                {props => this.renderContent(props)}
            </Documents>
        )
    }
}

// Constants
const YES = 'Yes'

// Utils
function createProperty(type, property, option1) {
    switch (type) {
        case 'Link':
            return function link(data) {
                return (
                    <a href={data[option1]} target="_blank">
                        {data[property]}
                    </a>
                )
            }
        case 'Html':
            return function html(data) {
                return <StructuredText value={data[property]} />
            }
        default:
            return data => data[property]
    }
}

function createColumn({ name, sortable, type, property, filterable, option1 }) {
    return {
        name: property,
        title: name,
        sortable: sortable === YES,
        filterable: filterable === YES,
        property: createProperty(type, property, option1),
    }
}
